  .navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgba(10, 10, 15, 0.8);
    backdrop-filter: blur(12px);
    border-bottom: 1px solid rgba(120, 119, 198, 0.1);
    transition: all 0.3s ease;
  }

  .navbar.scrolled {
    background: rgba(10, 10, 15, 0.95);
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.4);
  }

  .navbar-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 1.25rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .navbar-logo {
    font-size: 1.5rem;
    font-weight: 700;
    color: #fff;
    font-family: 'Space Grotesk', sans-serif;
    letter-spacing: -0.02em;
    cursor: pointer;
  }

  .logo-text {
    cursor: pointer;
  }

  .desktop-menu {
    display: flex;
    list-style: none;
    gap: 2.5rem;
    margin: 0;
    padding: 0;
    align-items: center;
  }

  .nav-menu {
    display: flex;
    list-style: none;
    gap: 2.5rem;
    margin: 0;
    padding: 0;
  }

  .nav-item {
    position: relative;
  }

  .nav-link {
    color: #a1a1aa;
    font-size: 0.9375rem;
    font-weight: 500;
    transition: color 0.3s ease;
    position: relative;
    cursor: pointer;
  }

  .nav-link:hover,
  .nav-link.active {
    color: #fff;
  }

  .nav-link::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(135deg, #7877c6, #5856d6);
    transition: width 0.3s ease;
  }

  .nav-link:hover::after,
  .nav-link.active::after {
    width: 100%;
  }

  .hamburger {
    display: none;
    flex-direction: column;
    gap: 5px;
    background: rgba(88, 86, 214, 0.1);
    border: 1px solid rgba(88, 86, 214, 0.2);
    border-radius: 10px;
    cursor: pointer;
    padding: 10px;
    transition: all 0.3s ease;
    width: 44px;
    height: 44px;
    justify-content: center;
    align-items: center;
  }

  .hamburger:hover {
    background: rgba(88, 86, 214, 0.2);
    border-color: rgba(88, 86, 214, 0.4);
    transform: scale(1.05);
  }

  .hamburger span {
    width: 22px;
    height: 2.5px;
    background: #fff;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 3px;
  } 

  .hamburger.active {
    background: rgba(239, 68, 68, 0.1);
    border-color: rgba(239, 68, 68, 0.3);
  }

  .hamburger.active:hover {
    background: rgba(239, 68, 68, 0.15);
  }

  .hamburger.active span:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }

  .hamburger.active span:nth-child(2) {
    opacity: 0;
    transform: scale(0);
  }

  .hamburger.active span:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }

  .mobile-menu {
    position: fixed;
    top: 70px;
    right: 0;
    width: 100%;
    height: calc(100vh - 70px);
    background: rgba(10, 10, 15, 0.98);
    backdrop-filter: blur(16px);
    z-index: 999;
    overflow-y: auto;
  }

  .mobile-menu-list {
    list-style: none;
    padding: 2rem 1.5rem;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }

  .mobile-menu-list li {
    color: #e4e4e7;
    font-size: 1.125rem;
    font-weight: 500;
    cursor: pointer;
    padding: 1.125rem 1.25rem;
    border-radius: 12px;
    transition: all 0.3s ease;
    border: 1px solid rgba(120, 119, 198, 0.15);
    background: rgba(255, 255, 255, 0.02);
    text-align: center;
  }

  .mobile-menu-list li:hover {
    background: rgba(88, 86, 214, 0.15);
    border-color: rgba(88, 86, 214, 0.3);
    color: #fff;
    transform: translateX(4px);
  }

  /* Mobile */
  @media (max-width: 768px) {
    .desktop-menu {
      display: none;
    }

    .hamburger {
      display: flex;
    }
    
    .navbar-container {
      padding: 1.25rem 1.5rem;
    }
    
    .navbar-logo {
      font-size: 1.25rem;
    }
  }
